let Datasets = {};

function addOptionalDataset(imageId: string, dataset: any): void {
    Datasets[imageId] = dataset
}

function getOptionalDataset(imageId: string): any {
    return Datasets[imageId]
}

function purgeOptionalDatasets(): void {
    Datasets = {};
}

export default {
    addOptionalDataset,
    getOptionalDataset,
    purgeOptionalDatasets,
};
