import parseImageId from './parseImageId';
import fileManager from './fileManager';
import loadFileRequest from "./loadFileRequest";
import {Types} from "@cornerstonejs/core";

/**
 * Convert array buffer to image. Returns a promise that resolves to an Image object for the bytes in arrayBuffer
 *
 * @param arrayBuffer - arrayBuffer with bytes for a web image (e.g. JPEG, PNG, etc)
 * @param onOpen - true if this loadImage is on file opening, false if is on file import.
 *                 If true, bypass JpegBaseLineDecoder since image to open works on lossless opening and reduces computing time
 * @returns {Promise} Promise that resolves to an Image object
 */
function ArraybufferToImage(arrayBuffer) {
    return new Promise((resolve, reject) => {

        const arrayBufferView = new Uint8Array(arrayBuffer);
        let image, urlCreator, imageUrl;

        //Try Image decoding
        try {
            image = new Image();
            urlCreator = window.URL || window.webkitURL;
            const blob = new Blob([arrayBufferView]);
            imageUrl = urlCreator.createObjectURL(blob);
            image.src = imageUrl;
        }
        catch (e) {
            console.error('ERROR::ARRAY_BUFFER_TO_IMAGE::IMAGE_LOADING', e)
        }

        image.onload = () => {
            resolve(image);
            // urlCreator.revokeObjectURL(imageUrl);
        };

        image.onerror = (error) => {
            // urlCreator.revokeObjectURL(imageUrl);
            reject(error);
        };
    });
}

const canvas = document.createElement('canvas');

/**
 * creates a cornerstone Image object for the specified Image and imageId
 *
 * @param image - An Image
 * @param imageId - the imageId for this image
 * @returns Cornerstone Image Object
 */
function createImage(image, imageId) {
    // extract the attributes we need
    const rows = image.naturalHeight;
    const columns = image.naturalWidth;

    function getPixelData () {
        const imageData = getImageData().data;
        const rgb_array = imageData.filter(function (v, i) {
            return !(i % 4 == 3);
        });

        const nb_pixels = canvas.height * canvas.width;
        const gray_array = new Uint8ClampedArray(nb_pixels);
        for (let i = 0; i < nb_pixels; i++)
            gray_array[i] = Math.floor((rgb_array[3 * i] + rgb_array[3 * i + 1] + rgb_array[3 * i + 2]) / 3);

        return gray_array
    }

    function getImageData () {
        let context;

        canvas.height = image.naturalHeight;
        canvas.width = image.naturalWidth;
        context = canvas.getContext('2d');
        context.drawImage(image, 0, 0);

        return context.getImageData(0, 0, image.naturalWidth, image.naturalHeight);
    }

    function getCanvas () {

        canvas.height = image.naturalHeight;
        canvas.width = image.naturalWidth;
        const context = canvas.getContext('2d');

        context.drawImage(image, 0, 0);

        return canvas;
    }

    // Extract the various attributes we need
    return {
        imageId,
        minPixelValue: 0,
        maxPixelValue: 255,
        slope: 1,
        intercept: 0,
        windowCenter: 128,
        windowWidth: 255,
        getPixelData,
        getCanvas,
        getImage: () => image,
        rows,
        columns,
        height: rows,
        width: columns,
        color: false,
        rgba: false,
        columnPixelSpacing: undefined,
        rowPixelSpacing: undefined,
        invert: false,
        sizeInBytes: rows * columns * 4
    };
}


export function loadPNGImage(image_id) : Types.IImageLoadObject{
    const imageLoadObject: Types.IImageLoadObject = {
        cancelFn: undefined,
        promise: undefined,
    };
    imageLoadObject.promise = new Promise((resolve, reject) => {

        loadFileRequest(image_id).then(arraybuffer => {
            ArraybufferToImage(arraybuffer).then(image => {
                const imageDataset = createImage(image, image_id)
                // @ts-ignore
                resolve(imageDataset)
            }, function (error) {
                // Reject the error
                reject({
                    error,
                    at: 'Arraybuffertoimage'
                });
            })
        }, function (error) {
            // Reject the error
            reject({
                error,
                at: 'loadFileRequest'
            });
        })
    })

    return imageLoadObject
}