import {loadPNGImage} from "../loadPNGImage";
import optionalDatasetManager from "../optionalDatasetManager";

// Add hardcoded meta data provider for color images
export default function hardcodedImgMetaDataProvider(type, imageId) {
    const colonIndex = imageId.indexOf(':');
    const scheme = imageId.substring(0, colonIndex);
    if (scheme !== 'pngfile') return;
    const dataset = optionalDatasetManager.getOptionalDataset(imageId)
    // console.log('RETRIEVED DATASET', dataset)

    // const imageLoadObject = loadPNGImage(imageId)
    // let c, r
    // imageLoadObject.promise.then(imo => {
    //     c = imo.columns
    //     r = imo.rows
    // })

    let bitsAllocated = dataset.BitsAllocated
    let bitsStored = dataset.BitsStored
    let columns = dataset.Columns
    let foruid = dataset.FrameOfReferenceUID
    let highBit = dataset.HighBit
    let photoInterpretation = dataset.PhotometricInterpretation
    let pixelRepresentation = dataset.PixelRepresentation
    let rows = dataset.Rows
    let sopcuid = dataset.SOPClassUID
    let sopiuid = dataset.SOPInstanceUID
    let samplesperpixel = dataset.SamplesPerPixel
    let studyuid = dataset.StudyInstanceUID
    let seriesuid = dataset.SeriesInstanceUID
    let framenumber = dataset.FrameNumber


    if (type === 'imagePixelModule') {
        const imagePixelModule = {
            pixelRepresentation: pixelRepresentation,
            bitsAllocated: bitsAllocated,
            bitsStored: bitsStored,
            highBit: highBit,
            photometricInterpretation: photoInterpretation,
            samplesPerPixel: samplesperpixel,
        };

        return imagePixelModule;
    } else if (type === 'generalSeriesModule') {
        const generalSeriesModule = {
            modality: 'OT',
            seriesNumber: 1,
            seriesDescription: 'Monochrome',
            seriesDate: '20190201',
            seriesTime: '120000',
            seriesInstanceUID: seriesuid,
        };

        return generalSeriesModule;
    } else if (type === 'imagePlaneModule') {
        const imagePlaneModule = {
            imageOrientationPatient: [1, 0, 0, 0, 1, 0],
            imagePositionPatient: [0, 0, 0],
            pixelSpacing: [1, 1],
            columnPixelSpacing: 1,
            rowPixelSpacing: 1,
            frameOfReferenceUID: foruid,
            columns: columns,
            rows: rows,
            rowCosines: [1, 0, 0],
            columnCosines: [0, 1, 0],
        };

        return imagePlaneModule;
    } else if (type === 'voiLutModule') {
        return {
            windowWidth: [128],
            windowCenter: [255],
        };
    } else if (type === 'modalityLutModule') {
        return {
            rescaleSlope: 1,
            rescaleIntercept: 0,
        };
    } else if (type === 'instance') {
        return {
            SOPInstanceUID: sopiuid,
            SeriesInstanceUID: seriesuid,
            StudyInstanceUID: studyuid,
            frameNumber: framenumber || 1,
        }
    }
    else {
        return undefined;
    }
}
