let files: Blob[] = [];
let Datasets = {};

function add(file: Blob, isPNG = false): string {
  const fileIndex = files.push(file);

  if(isPNG)
    return `pngfile:${fileIndex - 1}`
  return `dicomfile:${fileIndex - 1}`;
}

function get(index: number): Blob {
  return files[index];
}

function remove(index: number): void {
  files[index] = undefined;
}

function purge(): void {
  files = [];
}

export default {
  add,
  get,
  remove,
  purge,
};
